<template>
  <el-row justify="space-between" type="flex">
    <el-col :span="4">
      <h1>自习室座位预定</h1>
    </el-col>
    <el-col :span="2">
      <span>{{ $store.state.stuName }}</span>
      <el-dropdown trigger="click" @command="menu">
        <i class="el-icon-setting el-icon--right"></i>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item v-if="$store.state.stuId !== null" command="a">我的预约</el-dropdown-item>
          <el-dropdown-item v-if="$store.state.stuId === null" command="c">登录</el-dropdown-item>
          <el-dropdown-item command="b">退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-col>
  </el-row>
</template>

<script>
export default {
  name: "UserHeader",
  methods: {
    menu: function (command) {
      if (command === "b") {
        this.$store.commit("SET_ID", null);
        this.$store.commit("SET_NAME", null);
        location.href = "/logout"
      } else if(command === "a")
        this.$router.push('/history');
      else
        this.$router.push("/login");
    }
  }
}
</script>

