<template>
  <el-main>
    <el-form :inline="inline" :model="filterOptions" lable-with="24px">
      <el-form-item>
        <el-button round
                   type="primary"
                   @click="newStudyRoom()">添加
        </el-button>
      </el-form-item>
      <el-form-item label="楼栋">
        <el-input v-model="filterOptions.buildingNum" clearable>
        </el-input>
      </el-form-item>
      <el-form-item label="教室">
        <el-input v-model="filterOptions.classRoomNum" clearable>
        </el-input>
      </el-form-item>
      <el-form-item>
        <el-button circle icon="el-icon-search" @click="getStudyRooms(filterOptions)"></el-button>
      </el-form-item>
    </el-form>
    <el-table :border="form.border" :data="studyRooms" :stripe="form.strip">
      <!--      <el-table-column prop="id" label="ID" width="140">-->
      <!--      </el-table-column>-->
      <el-table-column
          type="index"
          width="50">
      </el-table-column>
      <el-table-column align="center" label="楼栋" prop="buildingNum">
      </el-table-column>
      <el-table-column align="center" label="教室" prop="classRoomNum">
      </el-table-column>
      <el-table-column align="center" label="开放时间" prop="startTime">
      </el-table-column>
      <el-table-column align="center" label="关闭时间" prop="endTime">
      </el-table-column>
      <el-table-column align="center" label="操作">
        <template v-slot="scope">
          <el-button size="mini"
                     type="success"
                     @click="update(scope.$index, scope.row)">修改
          </el-button>
          <el-popconfirm title="确定删除吗？"
                         @confirm="remove(scope.$index, scope.row)">
            <el-button slot="reference"
                       size="mini"
                       type="danger"
            >删除
            </el-button>
          </el-popconfirm>
          <el-button size="mini"
                     type="warning"
                     @click="goSeat(scope.$index, scope.row)">管理座位
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
        :current-page="pagination.currentPage"
        :hide-on-single-page="pagination.hideOnSingle"
        :page-size="pagination.pageSize"
        :total="pagination.total"
        layout="total, prev, pager, next"
        @prev-click="pagination.currentPage--"
        @next-click="pagination.currentPage++"
        @current-change="getStudyRooms">
    </el-pagination>
    <el-dialog :visible.sync="dialogVisible" title="">
      <el-form ref="dialogData" :model="dialogData" :rules="rules">
        <el-form-item label="ID">
          <el-input v-model="dialogData.id" :disabled="idInput" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="楼栋" prop="buildingNum">
          <el-input v-model="dialogData.buildingNum" :disabled="buildingNumInput" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="教室" prop="classRoomNum">
          <el-input v-model="dialogData.classRoomNum" :disabled="classRoomNumInput" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="开放时间" prop="startTime">
          <el-input v-model.number="dialogData.startTime" max="24" min="0" type="number"></el-input>
        </el-form-item>
        <el-form-item label="关闭时间" prop="endTime">
          <el-input v-model.number="dialogData.endTime" :min="dialogData.startTime + 1" max="24"
                    type="number"></el-input>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="saveStudyRoom()">确 定</el-button>
      </div>
    </el-dialog>
  </el-main>
</template>

<script>
import axios from "axios";

export default {
  name: "AdminStudyRoom",
  data() {
    let validateTime = (rule, value, callback) => {
      if (!Number.isInteger(value)) {
        callback(new Error('请输入数字值'));
      } else {
        if (value < 0 || value > 24) {
          callback(new Error('必须0-24之间的数字'));
        } else if (value <= this.dialogData.startTime) {
          callback(new Error("关闭时间不能小于开放时间"));
        } else
          callback();
      }
    };
    return {
      method: "POST",
      // 工具栏样式
      inline: true,
      // 表格样式
      form: {
        strip: true,
        border: true
      },
      dialogVisible: false,
      idInput: false,
      buildingNumInput: false,
      classRoomNumInput: false,
      dialogData: {
        id: null,
        buildingNum: null,
        classRoomNum: null,
        startTime: 7,
        endTime: 22
      },
      // 分页样式
      pagination: {
        total: null,
        hideOnSingle: false,
        pageSize: 10,
        currentPage: 1
      },
      // 筛选选项
      filterOptions: {
        buildingNum: null,
        classRoomNum: null,
      },
      // 规则

      rules: {
        buildingNum: [
          {required: true, message: '请输入楼栋', trigger: 'blur'}
        ],
        classRoomNum: [
          {required: true, message: '请输入教室', trigger: 'blur'}
        ],
        startTime: [
          {type: 'integer', required: true, min: 0, max: 24, trigger: 'blur', message: '应为0-24之间的整数'}
        ],
        endTime: [
          {validator: validateTime, trigger: 'blur'}
        ]
      },
      studyRooms: []
    }
  },
  methods: {
    getStudyRooms: function () {
      let that = this;
      axios.get("/studyroom", {
        params: {
          page: that.pagination.currentPage,
          rows: that.pagination.pageSize,
          buildingNum: that.filterOptions.buildingNum,
          classRoomNum: that.filterOptions.classRoomNum
        }
      }).then((res) => {
        if(res.data.code === '200') {
          that.studyRooms = res.data.data.records;
          that.pagination.total = res.data.data.total
        } else
          that.$message.error(res.data.msg);
      })
    },
    remove: function (index, row) {
      let that = this;
      axios.delete("/studyroom", {
        data: row
      }).then((res) => {
        if(res.data.code === '200')
          that.getStudyRooms(that.filterOptions);
        else if(res.data.code === '500')
          that.$message.error(res.data.msg + res.data.data);
        else
          that.$message.error(res.data.msg);
      })
    },
    update: function (index, row) {
      this.dialogData.id = row.id;
      this.dialogData.buildingNum = row.buildingNum;
      this.dialogData.classRoomNum = row.classRoomNum;
      this.dialogData.startTime = row.startTime;
      this.dialogData.endTime = row.endTime;
      this.idInput = true;
      this.buildingNumInput = true;
      this.classRoomNumInput = true;
      this.dialogVisible = true;
      this.method = "PUT"
    },
    newStudyRoom: function () {
      this.dialogData.id = null;
      this.dialogData.buildingNum = null;
      this.dialogData.classRoomNum = null;
      this.dialogData.startTime = 7;
      this.dialogData.endTime = 22;
      this.idInput = true;
      this.buildingNumInput = false;
      this.classRoomNumInput = false;
      this.method = "POST"
      this.dialogVisible = true;
    },
    saveStudyRoom: function () {
      let that = this;
      this.$refs.dialogData.validate((valid) => {
        if (valid) {
          if (this.method === "POST") {
            axios.post("/studyroom", this.dialogData)
                .then((res) => {
                  if(res.data.code === '200')
                    that.getStudyRooms(that.filterOptions);
                  else if(res.data.code === '500')
                    that.$message.error(res.data.msg + res.data.data);
                  else
                    that.$message.error(res.data.msg);
                });
          } else {
            axios.put("/studyroom", this.dialogData)
                .then((res) => {
                  if(res.data.code === '200')
                    that.getStudyRooms(that.filterOptions);
                  else if(res.data.code === '500')
                    that.$message.error(res.data.msg + res.data.data);
                  else
                    that.$message.error(res.data.msg);
                });
          }
          this.dialogVisible = false;
        } else
          return false;
      })
    },
    goSeat: function (index, row) {
      this.$router.push({
        path: '/seat',
        query: {
          studyRoomId: row.id,
          studyRoomInfo: row.buildingNum + row.classRoomNum
        }
      })
    }
  },
  mounted() {
    this.getStudyRooms();
  }
}
</script>

