<template>
  <el-main>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/ibooking' }">首页</el-breadcrumb-item>
    </el-breadcrumb>
    <el-divider></el-divider>
    <el-form ref="filter" :inline="inline" :model="filterOptions" :rules="rules" lable-with="24px">
      <el-form-item label="教室">
        <el-select v-model="filterOptions.studyRoomId" placeholder="请选择" @visible-change="queryStudyRoom">
          <el-option
              v-for="item in studyRooms"
              :key="item.id"
              :label="item.buildingNum + '-' + item.classRoomNum"
              :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="日期" prop="date">
        <el-date-picker
            v-model="filterOptions.date"
            :picker-options="pickerOptions"
            format="yyyy-MM-dd"
            placeholder="选择日期"
            range-separator="-"
            type="date"
            value-format="yyyy-MM-dd">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="时间" prop="startTime">
        <el-time-select
            v-model="filterOptions.startTime"
            :picker-options="{
                          start: '00:00',
                          step: '01:00',
                          end: '24:00'
                        }"
            placeholder="起始时间"
            value-format="HH:mm"
        ></el-time-select>
      </el-form-item>
      <el-form-item label=" - " prop="endTime">
        <el-time-select
            v-model="filterOptions.endTime"
            :picker-options="{
                          start: '00:00',
                          step: '01:00',
                          end: '24:00'
                        }"
            placeholder="结束时间"
            value-format="HH:mm"
        ></el-time-select>
      </el-form-item>
      <el-form-item label="插座">
        <el-select v-model="filterOptions.socket" clearable placeholder="请选择">
          <el-option
              key=true
              label="是"
              value=true>
          </el-option>
          <el-option
              key=false
              label="否"
              value=false>
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button circle icon="el-icon-search" type="primary" @click="getSeats()"></el-button>
      </el-form-item>
    </el-form>
    <el-table :border="form.border" :data="seats"
              :stripe="form.strip" empty-text="请输入想要预约的时间查询可用座位">
      <el-table-column align="center" label="楼栋" prop="buildingNum">
      </el-table-column>
      <el-table-column align="center" label="教室" prop="classRoomNum">
      </el-table-column>
      <el-table-column align="center" label="座位号" prop="num">
      </el-table-column>
      <el-table-column align="center" label="插座" prop="socket">
        <template v-slot="scope">
          <i :class="scope.row.socket === true ? 'el-icon-check' : 'el-icon-close'"></i>
        </template>
      </el-table-column>
      <el-table-column align="center" label="操作">
        <template v-slot="scope">
          <el-button size="mini"
                     type="success"
                     @click="reserve(scope.row)">预定
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
        :current-page="pagination.currentPage"
        :hide-on-single-page="pagination.hideOnSingle"
        :page-size="pagination.pageSize"
        :total="pagination.total"
        layout="total, prev, pager, next"
        @prev-click="pagination.currentPage--"
        @next-click="pagination.currentPage++"
        @current-change="getSeats">
    </el-pagination>
  </el-main>
</template>

<script>
import axios from "axios";

export default {
  name: "IBooking",
  data() {
    let validateTime = (rule, value, callback) => {
      if (value === null)
        callback(new Error("请输入结束时间"))
      else if (value <= this.filterOptions.startTime)
        callback(new Error("不能小于开始时间"))
      else if (parseInt(value) - parseInt(this.filterOptions.startTime) > 4)
        callback(new Error("一次不能超过4小时"))
      else
        callback();
    };
    let validateTime2 = (rule, value, callback) => {
      if (value === null)
        callback(new Error("请输入开始时间"))
      else if (this.filterOptions.date === new Date().toJSON().substring(0,10) && value <= new Date().toTimeString())
        callback(new Error("不能小于当前时间"))
      else
        callback();
    };
    return {
      // 工具栏样式
      inline: true,
      // 表格样式
      form: {
        strip: true,
        border: true
      },
      // 分页样式
      pagination: {
        total: null,
        hideOnSingle: false,
        pageSize: 10,
        currentPage: 1
      },
      // 筛选选项
      filterOptions: {
        date: null,
        startTime: null,
        endTime: null,
        studyRoomId: null,
        socket: null
      },
      // 下拉选择自习室
      studyRooms: [],
      // 座位
      seats: [],
      // 时间选择器禁用非法时间
      pickerOptions: {
        disabledDate(time) {
          let now = new Date();
          now.setHours(0);
          now.setMinutes(0);
          now.setSeconds(0);
          now.setMilliseconds(0);
          return time.getTime() < now.getTime();
        },
      },
      rules: {
        date: [
          {required: true, message: '请输入预约日期', trigger: 'blur'},
        ],
        startTime: [
          {validator: validateTime2, trigger: 'blur'}
        ],
        endTime: [
          {validator: validateTime, trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    // 下拉选择自习室
    queryStudyRoom: function (a) {
      if (a) {
        let that = this;
        axios.get("/studyroom").then((res) => {
          if(res.data.code === '200')
            that.studyRooms = res.data.data.records;
          else if(res.data.code === '408')
            that.$router.push("/login");
          else if (res.data.code === '500')
            that.$message.error(res.data.msg + res.data.data);
          else
            that.$message.error(res.data.msg)
        })
      }
    },
    // 获取座位
    getSeats: function () {
      let that = this;
      console.log(this.filterOptions)
      this.$refs.filter.validate((valid) => {
        if (valid) {
          axios.get("/ibooking/seat", {
            params: {
              page: that.pagination.currentPage,
              rows: that.pagination.pageSize,
              socket: that.filterOptions.socket,
              studyRoomId: that.filterOptions.studyRoomId,
              startTime: that.filterOptions.date + ' ' + that.filterOptions.startTime,
              endTime: that.filterOptions.date + ' ' + that.filterOptions.endTime,
            }
          }).then((res) => {
            if(res.data.code === '200') {
              that.seats = res.data.data;
              that.pagination.total = res.data.data.length;
            }
            else if(res.data.code === '408')
              that.$router.push("/login");
            else if (res.data.code === '500')
              that.$message.error(res.data.msg + res.data.data);
            else
              that.$message.error(res.data.msg)
          });
        } else
          return false;
      })
    },
    // 预定
    reserve: function (row) {
      let that = this;
      console.log(row);
      axios.post("/ibooking", {
        seatId: row.id,
        stuId: that.$store.state.stuId,
        start: that.filterOptions.date + ' ' + that.filterOptions.startTime,
        end: that.filterOptions.date + ' ' + that.filterOptions.endTime,
        sign: false,
        state: true
      }).then((res) => {
        if(res.data.code === '200')
          that.getSeats();
        else if(res.data.code === '408')
          that.$router.push("/login");
        else if (res.data.code === '500')
          that.$message.error(res.data.msg + res.data.data);
        else
          that.$message.error(res.data.msg)
      })
    }
  }
}
</script>

<style scoped>
.el-breadcrumb {
  margin-top: 14px;
  align-items: center;
}
</style>