import Vue from 'vue'
import Vuex from 'vuex'

const actions = {
};
const mutations = {
    SET_ID(state, value) {
        state.stuId = value;
    },
    SET_NAME(state, value) {
        state.stuName = value
    }
};
const state = {
    stuId: null,
    stuName:null
};

Vue.use(Vuex);

export default new Vuex.Store({
    actions,
    mutations,
    state
})