<template>
  <el-main>
    <el-form ref="filter"
             :inline="inline"
             :model="filterOptions" lable-with="24px">
      <el-form-item>
        <el-tag type="success">{{ studyRoomInfo }}</el-tag>
      </el-form-item>
        <el-form-item label="">
          <el-button round
                     type="primary"
                     @click="newSeat()">添加
          </el-button>
        </el-form-item>
        <el-form-item label="座位号" prop="studyRoomId">
          <el-input v-model.number="filterOptions.num" placeholder="座位号"></el-input>
        </el-form-item>
        <el-form-item label="插座">
          <el-select v-model="filterOptions.socket" clearable placeholder="请选择">
            <el-option
                key=true
                label="是"
                value=true>
            </el-option>
            <el-option
                key=false
                label="否"
                value=false>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button circle icon="el-icon-search" @click="getSeats(filterOptions)"></el-button>
        </el-form-item>
    </el-form>
    <el-table :border="form.border" :data="seats" :stripe="form.strip">
      <el-table-column
          type="index"
          width="50">
      </el-table-column>
      <!--      <el-table-column prop="id" label="ID" width="140">-->
      <!--      </el-table-column>-->
      <!--                <el-table-column align="center" label="自习室" prop="studyRoomId">-->
      <!--                </el-table-column>-->
      <el-table-column align="center" label="座位号" prop="num">
      </el-table-column>
      <el-table-column align="center" label="插座" prop="socket">
        <template v-slot="scope">
          <i :class="scope.row.socket === true ? 'el-icon-check' : 'el-icon-close'"></i>
        </template>
      </el-table-column>
      <el-table-column align="center" label="操作">
        <template v-slot="scope">
          <el-button size="mini"
                     type="success"
                     @click="update(scope.$index, scope.row)">修改
          </el-button>
          <el-popconfirm title="确定删除吗？"
                         @confirm="remove(scope.$index, scope.row)">
            <el-button slot="reference"
                       size="mini"
                       type="danger"
            >删除
            </el-button>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
        :current-page="pagination.currentPage"
        :hide-on-single-page="pagination.hideOnSingle"
        :page-size="pagination.pageSize"
        :total="pagination.total"
        layout="total, prev, pager, next"
        @prev-click="pagination.currentPage--"
        @next-click="pagination.currentPage++"
        @current-change="getSeats">
    </el-pagination>
    <el-dialog :visible.sync="dialogVisible" title="">
      <el-form ref="dialogData" :model="dialogData" :rules="rules">
        <el-form-item label="ID">
          <el-input v-model="dialogData.id" :disabled="idInput" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="自习室" prop="studyRoomId">
          <el-select v-model="dialogData.studyRoomId" :disabled="studyRoomIdInput" placeholder="请选择"
                     @visible-change="queryStudyRoom">
            <el-option
                v-for="item in studyRooms"
                :key="item.id"
                :label="item.buildingNum + '-' + item.classRoomNum"
                :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="座位号" prop="num">
          <el-input v-model.number="dialogData.num" type="number">
          </el-input>
        </el-form-item>
        <el-form-item label="是否有插座" prop="socket">
          <el-switch v-model="dialogData.socket" active-color="#13ce66"
                     inactive-color="#ff4949">
          </el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="saveSeat()">确 定</el-button>
      </div>
    </el-dialog>
  </el-main>
</template>

<script>
import axios from "axios";

export default {
  name: "AdminSeat",
  data() {
    return {
      studyRoomInfo: null,
      method: "POST",
      // 工具栏样式
      inline: true,
      // 表格样式
      form: {
        strip: true,
        border: true
      },
      studyRoomId: null,
      dialogVisible: false,
      idInput: false,
      studyRoomIdInput: false,
      dialogData: {
        id: null,
        studyRoomId: this.studyRoomId,
        num: null,
        socket: false
      },
      // 分页样式
      pagination: {
        total: null,
        hideOnSingle: false,
        pageSize: 10,
        currentPage: 1
      },
      // 筛选选项
      filterOptions: {
        num: null,
        studyRoomId: this.studyRoomId,
        socket: null
      },
      // 座位
      seats: [],
      rules: {
        studyRoomId: [
          {required: true, message: '请输入自习室ID', trigger: 'blur'}
        ],
        socket: [
          {required: true, message: '请输入是否有插座', trigger: 'blur'}
        ],
        num: [
          {required: true, message: '请输入座位号', trigger: 'blur'},
          {type: 'number', min: 1, max: 150, message: '请输入1-150之间的数字', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    // 获取座位
    getSeats: function () {
      let that = this;
      this.$refs.filter.validate((valid) => {
        if (valid) {
          axios.get("/seat", {
            params: {
              page: that.pagination.currentPage,
              rows: that.pagination.pageSize,
              studyRoomId: this.studyRoomId,
              num: that.filterOptions.num,
              socket: that.filterOptions.socket
            }
          }).then((res) => {
            that.seats = res.data.data.records;
            that.pagination.total = res.data.data.total
          })
        } else return false;
      })
    },
    // 删除
    remove: function (index, row) {
      let that = this;
      axios.delete("/seat", {
        data: row
      }).then((res) => {
        if(res.data.code === '200')
          that.getSeats();
        else if(res.data.code === '500')
          that.$message.error(res.data.msg + res.data.data);
        else
          that.$message.error(res.data.msg);
      })
    },
    update: function (index, row) {
      this.dialogData.id = row.id;
      this.dialogData.num = row.num;
      this.dialogData.socket = row.socket;
      this.dialogData.studyRoomId = row.studyRoomId;
      this.idInput = true;
      this.studyRoomIdInput = true;
      this.dialogVisible = true;
      this.method = "PUT"
    },
    newSeat: function () {
      this.dialogData.id = null;
      this.dialogData.num = null;
      this.dialogData.socket = false;
      this.dialogData.studyRoomId = this.studyRoomId;
      this.idInput = true;
      this.studyRoomIdInput = true;
      this.dialogVisible = true;
      this.method = "POST"
    },
    saveSeat: function () {
      let that = this;
      this.$refs.dialogData.validate((valid) => {
        if (valid) {
          if (this.method === "POST") {
            axios.post("/seat", this.dialogData)
                .then((res) => {
                  if(res.data.code === '200')
                    that.getSeats();
                  else if(res.data.code === '500')
                    that.$message.error(res.data.msg + res.data.data);
                  else
                    that.$message.error(res.data.msg);
                });
          } else {
            axios.put("/seat", this.dialogData)
                .then((res) => {
                  if(res.data.code === '200')
                    that.getSeats();
                  else if(res.data.code === '500')
                    that.$message.error(res.data.msg + res.data.data);
                  else
                    that.$message.error(res.data.msg);
                });
          }
          this.dialogVisible = false;
        } else return false;
      })
    }
  },
  mounted() {
    this.studyRoomId = this.$route.query.studyRoomId;
    this.studyRoomInfo = this.$route.query.studyRoomInfo;
    this.getSeats();
  }
}
</script>

