<template>
  <el-main>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/ibooking' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>历史预约</el-breadcrumb-item>
    </el-breadcrumb>
    <el-divider></el-divider>
    <el-form ref="filter" :inline="inline" :model="filterOptions" lable-with="24px">
      <el-form-item label="是否签到">
        <el-select v-model="filterOptions.sign" clearable placeholder="请选择">
          <el-option
              key=true
              label="是"
              value=true>
          </el-option>
          <el-option
              key=false
              label="否"
              value=false>
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="有效">
        <el-select v-model="filterOptions.state" clearable placeholder="请选择">
          <el-option
              key=true
              label="是"
              value=true>
          </el-option>
          <el-option
              key=false
              label="否"
              value=false>
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button circle icon="el-icon-search" type="primary" @click="getBookings()"></el-button>
      </el-form-item>
    </el-form>
    <el-table :border="form.border"
              :data="bookings"
              :default-sort="{prop: 'start', order: 'descending'}"
              :row-class-name="tableRowClassName"
              :stripe="form.strip">
      <el-table-column align="center" label="座位" prop="seatId">
      </el-table-column>
      <el-table-column align="center" label="开始时间" prop="start" sortable>
      </el-table-column>
      <el-table-column align="center" label="结束时间" prop="end">
      </el-table-column>
      <el-table-column align="center" label="是否签到" prop="sign">
        <template v-slot="scope">
          <i :class="scope.row.sign === true ? 'el-icon-check' : 'el-icon-close'"></i>
        </template>
      </el-table-column>
      <el-table-column align="center" label="操作">
        <template v-slot="scope">
          <el-button v-if="scope.row.state"
                     :disabled="scope.row.sign"
                     size="mini"
                     type="success"
                     @click="sign(scope.row)">签到
          </el-button>
          <el-popconfirm v-if="scope.row.state"
                         title="确定删除吗？"
                         @confirm="remove(scope.row)">
            <el-button slot="reference"
                       size="mini"
                       type="danger">取消
            </el-button>
          </el-popconfirm>
          <el-button v-if="!scope.row.state"
                     size="mini"
                     type="warning"
                     @click="add(scope.row)">再次预订
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
        :current-page="pagination.currentPage"
        :hide-on-single-page="pagination.hideOnSingle"
        :page-size="pagination.pageSize"
        :total="pagination.total"
        layout="total, prev, pager, next"
        @prev-click="pagination.currentPage--"
        @next-click="pagination.currentPage++"
        @current-change="getBookings">
    </el-pagination>
    <el-dialog :visible.sync="dialogVisible" title="">
    <el-form ref="dialogData" :model="dialogData" :rules="rules">
      <el-form-item label="日期" prop="date">
        <el-date-picker
            v-model="dialogData.date"
            :picker-options="pickerOptions"
            format="yyyy-MM-dd"
            placeholder="选择日期"
            range-separator="-"
            type="date"
            value-format="yyyy-MM-dd"
            @blur="checkTime()">
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-card class="box-card" v-if="showTime">
          可用时间段：
          <div v-for="o in validTime" :key="o" class="text item">
            {{o[0] + ' - ' + o[1] }}
          </div>
        </el-card>
      </el-form-item>
      <el-form-item label="起始时间" prop="startTime">
        <el-time-select
            v-model="dialogData.startTime"
            :picker-options="{
                          start: '00:00',
                          step: '01:00',
                          end: '24:00'
                        }"
            placeholder="起始时间"
            value-format="HH:mm"
        ></el-time-select>
      </el-form-item>
      <el-form-item label="结束时间" prop="endTime">
        <el-time-select
            v-model="dialogData.endTime"
            :picker-options="{
                          start: '00:00',
                          step: '01:00',
                          end: '24:00'
                        }"
            placeholder="结束时间"
            value-format="HH:mm"
        ></el-time-select>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false">取 消</el-button>
      <el-button type="primary" @click="reserve()">确 定</el-button>
    </div>
  </el-dialog>
  </el-main>
</template>

<script>
import axios from "axios";

export default {
  name: "UserHistory",
  data() {
    let validateTime = (rule, value, callback) => {
      if (value === null)
        callback(new Error("请输入结束时间"))
      else if (value <= this.dialogData.startTime)
        callback(new Error("不能小于开始时间"))
      else if (parseInt(value) - parseInt(this.dialogData.startTime) > 4)
        callback(new Error("一次不能超过4小时"));
      else if(!validate())
        callback(new Error("时间段不可用！"));
      else
        callback();
    };
    let validateTime2 = (rule, value, callback) => {
      if (value === null)
        callback(new Error("请输入开始时间"))
      else if (this.dialogData.date === new Date().toJSON().substring(0,10) && value <= new Date().toTimeString())
        callback(new Error("不能小于当前时间"))
      else
        callback();
    };
    let validate = () => {
      for(let i=0;i<this.validTime.length;i++) {
        let x = this.validTime[i];
        if(parseInt(this.dialogData.startTime) >= x[0] &&
            parseInt(this.dialogData.endTime) <= x[1])
          return true;
      }
      return false;
    }
    return {
      inline: true,
      dialogVisible: false,
      dialogData: {
        seatId: null,
        stuId: this.$store.state.stuId,
        startTime: null,
        date: null,
        endTime: null,
      },
      // 表格样式
      form: {
        strip: false,
        border: true
      },
      // 分页样式
      pagination: {
        total: null,
        hideOnSingle: false,
        pageSize: 10,
        currentPage: 1
      },
      // 筛选选项
      filterOptions: {
        state: null,
        sign: null
      },
      // 座位
      bookings: [],
      rules: {
        date: [
          {required: true, message: '请输入预约日期', trigger: 'blur'},
        ],
        startTime: [
          {validator: validateTime2, trigger: 'blur'}
        ],
        endTime: [
          {validator: validateTime, trigger: 'blur'}
        ]
      },
      pickerOptions: {
        disabledDate(time) {
          let now = new Date();
          now.setHours(0);
          now.setMinutes(0);
          now.setSeconds(0);
          now.setMilliseconds(0);
          return time.getTime() < now.getTime();
        },
      },
      validTime: [],
      showTime: false
    }
  },
  methods: {
    // 菜单点击事件
    getBookings: function () {
      let that = this;
      axios.get("/ibooking", {
        params: {
          page: that.pagination.currentPage,
          rows: that.pagination.pageSize,
          sign: that.filterOptions.sign,
          state: that.filterOptions.state
        }
      }).then((res) => {
        if(res.data.code === '200') {
          that.bookings = res.data.data;
          that.pagination.total = res.data.data.length;
        }
        else if(res.data.code === '408')
          that.$router.push("/login");
        else
          that.$message.error(res.data.msg)
      });
    },
    //
    sign: function (row) {
      let that = this;
      row.sign = true;
      axios.put("/ibooking", row).then((res) => {
        if (res.data.code === "200")
          that.$message.success(res.data.msg)
        else if(res.data.code === '408')
          that.$router.push("/login");
        else if (res.data.code === '500')
          that.$message.error(res.data.msg + res.data.data);
        else {
          that.$message.error(res.data.msg)
        }
      })
    },
    remove(row) {
      let that = this;
      row.state = false;
      axios.put("/ibookingState", row).then((res) => {
        if (res.data.code === "200") {
          that.$message.success(res.data.msg)
        }
        else if(res.data.code === '408')
          that.$router.push("/login");
        else if (res.data.code === '500')
          that.$message.error(res.data.msg + res.data.data);
        else {
          that.$message(res.data.msg)
          row.state = true;
        }
      })
    },
    add(row) {
      this.dialogData.seatId = row.seatId;
      this.dialogVisible = true
    },
    checkTime() {
      let that = this;
      if (that.dialogData.date != null) {
        axios.get("/ibooking/dateSeat", {
          params: {
            seatId: that.dialogData.seatId,
            startTime: that.dialogData.date
          }
        }).then((res) => {
          if(res.data.code === '200') {
            that.validTime = res.data.data;
            that.showTime = true;
          }
          else if(res.data.code === '408')
            that.$router.push("/login");
          else if (res.data.code === '500')
            that.$message.error(res.data.msg + res.data.data);
          else
            that.$message.error(res.data.msg)
        })
      }
    },
    tableRowClassName({row}) {
      if (!row.state)
        return 'warning-row';
      else
        return 'success-row';
    },
    reserve: function () {
      let that = this;
      this.$refs.dialogData.validate((valid) => {
        if (valid) {
          axios.post("/ibooking", {
            seatId: that.dialogData.seatId,
            stuId: that.dialogData.stuId,
            start: that.dialogData.date + ' ' + that.dialogData.startTime,
            end: that.dialogData.date + ' ' + that.dialogData.endTime,
            sign: false,
            state: true
          }).then((res) => {
            if(res.data.code === '200') {
              that.getBookings();
              that.dialogVisible = false;
              that.showTime = false;
            }
            else if(res.data.code === '408')
              that.$router.push("/login");
            else if (res.data.code === '500')
              that.$message.error(res.data.msg + res.data.data);
            else
              that.$message.error(res.data.msg)
          })
        } else
          return false;
      })
    },
  },
  mounted: function () {
    this.getBookings();
  }
}
</script>

<style scoped>
.el-breadcrumb {
  margin-top: 14px;
  align-items: center;
}
.el-row {
  height: 100%;
}

.el-table .warning-row {
  background: oldlace;
}

.el-table .success-row {
  background: #f0f9eb;
}

</style>