<template>
  <el-main>
    <el-form :inline="inline" :model="filterOptions" lable-with="24px">
      <el-form-item>
        <el-button round
                   type="primary"
                   @click="newStudent()">添加
        </el-button>
      </el-form-item>
      <el-form-item label="学号">
        <el-input v-model="filterOptions.stuNum" clearable></el-input>
      </el-form-item>
      <el-form-item label="姓名">
        <el-input v-model="filterOptions.name" clearable></el-input>
      </el-form-item>
      <el-form-item label="信用">
        <el-input v-model="filterOptions.credit" clearable></el-input>
      </el-form-item>

      <el-form-item>
        <el-button circle icon="el-icon-search" @click="getStudents(filterOptions)"></el-button>
      </el-form-item>
    </el-form>
    <el-table :border="form.border" :data="students" :stripe="form.strip">
      <el-table-column
          type="index"
          width="50">
      </el-table-column>
      <!--                <el-table-column prop="id" label="ID" width="140">-->
      <!--                </el-table-column>-->
      <el-table-column align="center" label="学号" prop="stuNum">
      </el-table-column>
      <el-table-column align="center" label="姓名" prop="name">
      </el-table-column>
      <el-table-column align="center" label="邮箱" prop="email">
      </el-table-column>
      <el-table-column align="center" label="失信次数" prop="credit">
      </el-table-column>
      <el-table-column align="center" label="操作">
        <template v-slot="scope">
          <el-button size="mini"
                     type="success"
                     @click="update(scope.$index, scope.row)">修改
          </el-button>
          <el-popconfirm title="确定删除吗？"
                         @confirm="remove(scope.$index, scope.row)">
            <el-button slot="reference"
                       size="mini"
                       type="danger"
            >删除
            </el-button>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
        :current-page="pagination.currentPage"
        :hide-on-single-page="pagination.hideOnSingle"
        :page-size="pagination.pageSize"
        :total="pagination.total"
        layout="total, prev, pager, next"
        @prev-click="pagination.currentPage--"
        @next-click="pagination.currentPage++"
        @current-change="getStudents">
    </el-pagination>
    <el-dialog :visible.sync="dialogVisible" title="" top="10px">
      <el-form ref="dialogData" :model="dialogData" :rules="rules">
        <el-form-item label="ID">
          <el-input v-model="dialogData.id" :disabled="idInput" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="学号" prop="stuNum">
          <el-input v-model="dialogData.stuNum" :disabled="stuNumInput" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="姓名" prop="name">
          <el-input v-model="dialogData.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="dialogData.email" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="dialogData.password" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="信用">
          <el-input v-model="dialogData.credit" :disabled="creditInput" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="saveStudent()">确 定</el-button>
      </div>
    </el-dialog>
  </el-main>
</template>

<script>
import axios from 'axios'

export default {
  name: "AdminStudent",
  data() {
    return {
      method: "POST",
      // 工具栏样式
      inline: true,
      // 表格样式
      form: {
        strip: true,
        border: true
      },
      dialogVisible: false,
      idInput: false,
      stuNumInput: false,
      creditInput: false,
      dialogData: {
        id: null,
        stuNum: null,
        name: null,
        email: null,
        credit: null,
        password: null
      },
      // 分页样式
      pagination: {
        total: null,
        hideOnSingle: false,
        pageSize: 10,
        currentPage: 1
      },
      // 筛选选项
      filterOptions: {
        stuNum: null,
        name: null,
        credit: null
      },
      students: [],
      rules: {
        stuNum: [
          {required: true, message: '请输入学号', trigger: 'blur'},
        ],
        name: [
          {required: true, message: '请输入姓名', trigger: 'blur'},
          {min: 2, max: 16, message: '长度为2-16个字符', trigger: 'blur'}
        ],
        email: [
          {required: true, message: '请输入邮箱', trigger: 'blur'},
          {type: 'email', message: '请输入正确的邮箱格式', trigger: 'blur'}
        ],
        password: [
          {required: true, message: '请输入密码', trigger: 'blur'},
          {min: 6, message: '长度至少为6字符', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    getStudents: function () {
      let that = this;
      axios.get("/student", {
        params: {
          page: that.pagination.currentPage,
          rows: that.pagination.pageSize,
          stuNum: that.filterOptions.stuNum,
          name: that.filterOptions.name,
          credit: that.filterOptions.credit
        }
      }).then((res) => {
        if (res.data.code === '200') {
          that.students = res.data.data.records;
          that.pagination.total = res.data.data.total;
        } else
          that.$message.error(res.data.msg);
      })
    },
    remove: function (index, row) {
      let that = this;
      axios.delete("/student", {
        data: row
      }).then((res) => {
        if (res.data.code === '200')
          that.getStudents(that.filterOptions);
        else if (res.data.code === '500')
          that.$message.error(res.data.msg + res.data.data);
        else
          that.$message.error(res.data.msg);
      })
    },
    update: function (index, row) {
      this.dialogData.id = row.id;
      this.dialogData.name = row.name;
      this.dialogData.stuNum = row.stuNum;
      this.dialogData.email = row.email;
      this.dialogData.credit = row.credit;
      this.dialogData.password = row.password;
      this.idInput = true;
      this.stuNumInput = true;
      this.dialogVisible = true;
      this.creditInput = true;
      this.method = "PUT"
    },
    newStudent: function () {
      this.dialogData.id = null;
      this.dialogData.name = null;
      this.dialogData.stuNum = null;
      this.dialogData.email = null;
      this.dialogData.password = null;
      this.dialogData.credit = 0;
      this.idInput = true;
      this.stuNumInput = false;
      this.creditInput = true;
      this.dialogVisible = true;
      this.method = "POST"
    },
    saveStudent: function () {
      let that = this;
      this.$refs.dialogData.validate((valid) => {
        if (valid) {
          if (this.method === "POST") {
            axios.post("/student", this.dialogData)
                .then((res) => {
                  if (res.data.code === '200')
                    that.getStudents(that.filterOptions)
                  else if (res.data.code === '500')
                    that.$message.error(res.data.msg + res.data.data);
                  else
                    that.$message.error(res.data.msg);
                });
          } else {
            axios.put("/student", this.dialogData)
                .then((res) => {
                  if (res.data.code === '200')
                    that.getStudents(that.filterOptions)
                  else if (res.data.code === '500')
                    that.$message.error(res.data.msg + res.data.data);
                  else
                    that.$message.error(res.data.msg);
                });
          }
          this.dialogVisible = false;
        } else return false;
      });
    }
  },
  mounted() {
    this.getStudents();
  }
}
</script>

