<template>
  <div class="root">
  <div class="loginBox">
    <el-form ref="stu" :model="stu" :rules="rules" class="loginForm">
      <el-form-item>
        <h1>登录</h1>
      </el-form-item>
      <el-form-item prop="stuNum" required>
        <el-input v-model="stu.stuNum"
                  placeholder="请输入学号" prefix-icon="el-icon-user"></el-input>
      </el-form-item>
      <el-form-item prop="password" required>
        <el-input v-model="stu.password" placeholder="请输入密码"
                  prefix-icon="el-icon-lock" show-password></el-input>
      </el-form-item>
      <el-form-item>
        <el-button class="button"
                   type="primary"
                   @click="login('stu')">登录
        </el-button>
      </el-form-item>
      <el-form-item>
        <el-button class="button" @click="register">注册</el-button>
      </el-form-item>
    </el-form>
  </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "LoginFrame",
  data(){
    return {
    stu: {
      stuNum: ``,
      password: ''
    },
    rules: {
      stuNum: [
        {required: true, message: '请输入学号', trigger: 'blur'}
      ],
      password: [
        {required: true, message: '请输入密码', trigger: 'blur'},
        {min: 6, message: '长度至少为6字符', trigger: 'blur'}
      ]
    }
  }},
  methods: {
    login: function () {
      let that = this;
      this.$refs.stu.validate((valid) => {
        if (valid) {
          axios.post("/login/validate", that.stu)
              .then((res) => {
                that.$message({
                  message: res.data.msg,
                  type: res.data.code === '200' ? 'success' : 'error'
                });
                if(res.data.code === '200') {
                  console.log(res)
                  this.$store.commit("SET_ID", res.data.data.id);
                  this.$store.commit("SET_NAME", res.data.data.name);
                  console.log(this.$store.state.stuId, this.$store.state.stuName);
                  this.$router.push("/user")
                }
              })
        } else {
          return false;
        }
      });
    },
    register: function () {
      this.$message({message: "请联系管理员", type: 'warning'});
    }
  }
}
</script>

<style scoped>
h1 {
  text-align: center;
  margin: 0 0;
  padding: 0 0;
}
button {
  width: 100%;
}

.root {
  background-color: #66CDAA;
  height: 100%;
}

.loginBox {
  width: 400px;
  height: 350px;
  background-color: #fff;
  border-radius: 15px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%)
}

.loginForm {
  position: absolute;
  height: auto;
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
}
</style>